.overlay {
  width: 100vw;
  background-color: var(--bg-hero-primary-color);
  @media screen and (max-width: 767px) {
    overflow: hidden;
    height: 500px;
  }

  @media screen and (min-width: 768px) {
    height: 400px;
  }

  @media screen and (min-width: 1200px) {
    height: 665px;
  }
}
.hero__container {
  position: relative;
  z-index: 2;
  @media screen and (min-width: 768px) {
    height: 100%;
  }
}
.hero_wrap {
  padding: 194px 0px 156px 0px;

  @media screen and (min-width: 768px) {
    display: flex;
    position: relative;
    padding: 0;
    height: 100%;
  }
}
.hero__title {
  width: 218px;
  margin-bottom: 24px;
  color: var(--text-primary-color);
  @include font(26px, 29.77px, 0.04em, 400, var(--font-secondary));
  text-transform: uppercase;
  & span {
    color: var(--text-secondary-color);
    @include font(48px, 46.32px, null, 400, var(--font-secondary));
  }
  &::after {
    content: ' ';
    position: absolute;
    z-index: -1;
    width: 346px;
    height: 344px;
    top: 83px;
    left: 36px;
    border-radius: 50%;
    background-color: var(--bg-hero-secondary-color);
  }
  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 25px;
    width: 162px;

    & span {
      font-size: 34px;
      line-height: 90.5%;
      letter-spacing: 0.04em;
      text-transform: uppercase;
    }

    &::after {
      width: 324px;
      height: 322px;
      top: -29px;
      transform: translate(8%, 19%);
      top: -29px;
      left: 255px;
    }
  }

  @media screen and (min-width: 1200px) {
    font-size: 38px;
    line-height: 1.15;
    width: 277px;

    & span {
      font-size: 58px;
      line-height: 1.11;
      letter-spacing: 0.04em;
      text-transform: uppercase;
    }

    &::after {
      width: 538px;
      height: 538px;
      top: -53px;
      left: 380px;
    }
  }
}
.hero__button {
  @include font(8px, 10px, null, 400, var(--font-primary));
  display: inline-block;
  color: var(--text-primary-color);
  padding: 8px 15px;
  background: var(--btn-bg-secondary-color);
  border-radius: 20px;
  transition: background-color 250ms var(--primary-cubic-bezier);
  & + & {
    margin-left: 8px;
  }
  &:hover,
  &:focus {
    background-color: var(--btn-bg-hover-secondary-color);
  }
  &_red {
    color: var(--text-secondary-color);
    background-color: var(--bg-primary-color);
  }
  &_red:hover,
  &_red:focus {
    background-color: var(--btn-bg-hover-primary-color);
  }
  @media screen and (min-width: 1200px) {
    padding: 11px 23px;
    font-size: 14px;
    line-height: 1.28;
  }
}

.second_box {
  display: none;
  position: absolute;
  bottom: 28px;

  @media screen and (min-width: 768px) {
    display: block;
  }

  @media screen and (min-width: 1200px) {
    padding-left: 31px;
  }
}

