.box__number {
  font-weight: 400;
  font-family: Titan One, cursive;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--text-secondary-color);

  @media screen and (min-width: 1200px) {
    font-size: 46px;
    line-height: 1.15;
  }
}

.box__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  text-align: right;
  color: var(--text-primary-color);

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.28;
    min-inline-size: max-content;
  }
}

.hero__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.5;
  color: var(--text-primary-color);

  @include min-media($desktop) {
    font-size: 14px;
    line-height: 1.4;
  }
}

.white-elipse-second,
.white-elipse-first {
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: var(--bg-primary-color);
  position: relative;

  @media screen and (min-width: 1200px) {
    width: 30px;
    height: 30px;
  }
}

.white-elipse-first {
  margin-bottom: 35px;

  @media screen and (min-width: 1200px) {
    margin-bottom: 62px;
  }
}

.box {
  position: absolute;
  right: 56%;
  top: 5%;
}

.hero_right {
  display: none;
  position: absolute;
  right: 0;
  flex-direction: column;
  bottom: 0;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.right_first_box {
  right: 0;
  width: 51px;
  height: 104px;
  margin-bottom: 72px;
  padding-left: 150px;

  @media screen and (min-width: 1200px) {
    margin-bottom: 109px;
    padding-left: 260px;
    height: 168px;
  }
}

.box-text-wrap {
  width: 114px;
  height: 73px;
  position: absolute;
  bottom: 8px;
  right: -130px;

  @media screen and (min-width: 1200px) {
    width: 200px;
    bottom: 55px;
    right: -210px;
  }
}

.first_box {
  @media screen and (min-width: 768px) {
    padding-top: 102px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 171px;
    padding-left: 69px;
  }
}

.hero__img {
  position: absolute;
  z-index: -1;

  @media screen and (max-width: 767px) {
    top: 0px;
    left: 0px;
    transform: translate(80%, 22%);
  }

  @media screen and (min-width: 768px) {
    right: 213px;
    bottom: 0;
    animation: pulsing 2s;
  }

  @media screen and (min-width: 1200px) {
    right: 330px;
  }
}

@media screen and (min-width: 768px) {
  @keyframes pulsing {
    0% {
      transform: translateX(500%);
    }

    50% {
      transform: translateX(-200%);
    }

    100% {
      transform: translateX(0);
    }
  }
}

