.hero__text--details-btn {
  display: flex;
  position: absolute;
  top: 115%;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary-color);
  font-size: 8px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: var(--btn-bg-primary-color);
  cursor: pointer;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

  @include min-media($desktop) {
    width: 26px;
    height: 26px;
    font-size: 11px;
    top: 125%;
  }
}

.hero__text--details-btn:hover {
  transform: scale(1.2);
}

details[open] .hero__text--details-btn {
  display: none;
  // left: -40px;
}

// Анимация sooooooo tasty!
.hero__text-animation {
  display: none;

  @include min-media($tablet) {
    display: inline;
    position: absolute;
    left: 0;
    bottom: 86px;
    @include font(12px, 16px, null, 400, var(--font-secondary));
    text-transform: uppercase;
    color: var(--text-secondary-color);
  }

  @include min-media($desktop) {
    bottom: 151px;
    @include font(21px, 25px);
  }
}

.text-animation__item {
  position: absolute;
  opacity: 0;
  animation-duration: 1000ms;
  animation-timing-function: ease;
  animation-fill-mode: forwards;

  @include min-media($tablet) {
    &--1 {
      left: 13px;
      bottom: 11px;
      animation-name: rotate-left-text;
      animation-delay: 2000ms;
    }
    &--2 {
      left: 21px;
      bottom: 11px;
      animation-name: rotate-left-text;
      animation-delay: 2300ms;
    }
    &--3 {
      left: 30px;
      bottom: 11px;
      animation-name: rotate-left-text;
      animation-delay: 2600ms;
    }
    &--4 {
      left: 39px;
      bottom: 11px;
      animation-name: flight-text;
      animation-delay: 2900ms;
    }
    &--5 {
      left: 48px;
      bottom: 11px;
      animation-name: rotate-right-text;
      animation-delay: 3200ms;
    }
    &--6 {
      left: 57px;
      bottom: 11px;
      animation-name: rotate-right-text;
      animation-delay: 3500ms;
    }
    &--7 {
      left: 66px;
      bottom: 11px;
      animation-name: rotate-right-text;
      animation-delay: 3800ms;
    }
    &--8 {
      left: 32px;
      bottom: 0px;
      animation-name: flight-text;
      animation-delay: 4300ms;
    }
  }

  @include min-media($desktop) {
    &--1 {
      left: 22px;
      bottom: 20px;
      animation-name: rotate-left-text;
      animation-delay: 2000ms;
    }
    &--2 {
      left: 36px;
      bottom: 20px;
      animation-name: rotate-left-text;
      animation-delay: 2300ms;
    }
    &--3 {
      left: 52px;
      bottom: 20px;
      animation-name: rotate-left-text;
      animation-delay: 2600ms;
    }
    &--4 {
      left: 68px;
      bottom: 20px;
      animation-name: flight-text;
      animation-delay: 2900ms;
    }
    &--5 {
      left: 84px;
      bottom: 20px;
      animation-name: rotate-right-text;
      animation-delay: 3200ms;
    }
    &--6 {
      left: 100px;
      bottom: 20px;
      animation-name: rotate-right-text;
      animation-delay: 3500ms;
    }
    &--7 {
      left: 116px;
      bottom: 20px;
      animation-name: rotate-right-text;
      animation-delay: 3800ms;
    }
    &--8 {
      left: 56px;
      bottom: 0px;
      animation-name: flight-text;
      animation-delay: 4300ms;
    }
  }
}

@keyframes flight-text {
  0% {
    transform: translateY(0) scale(0);
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-500%) scale(500%);
  }
}

@keyframes rotate-left-text {
  0% {
    opacity: 1;
    transform: translateX(0) translateY(0) scale(0) rotateZ(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-500%) translateY(-500%) scale(500%) rotateZ(-45deg);
  }
}

@keyframes rotate-right-text {
  0% {
    opacity: 1;
    transform: translateX(0) translateY(0) scale(0) rotateZ(0);
  }
  100% {
    opacity: 0;
    transform: translateX(500%) translateY(-500%) scale(500%) rotateZ(45deg);
  }
}
