.advantages {
  background-color: var(--bg-secondary-color);
  margin-top: 0;
  padding-bottom: 97px;
  @media screen and (min-width: 768px) {
    margin-top: 0;
    padding-bottom: 140px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 0;
    padding-bottom: 139px;

    & .container {
      padding-left: 75px;
      padding-right: 75px;
    }
  }
}

@media screen and (min-width: 768px) {
  .advantages__list {
    display: flex;
    margin-top: 0;
    margin-right: -30px;
    margin-left: -30px;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__list {
    margin-top: 0;
    margin-right: -15px;
    margin-left: -15px;
  }
}
.advantages__item {
  @media screen and (min-width: 768px) {
    width: calc(100% / 3 - 60px);
    margin-left: 30px;
    margin-right: 30px;
  }
  @media screen and (min-width: 1200px) {
    width: calc(100% / 3 - 30px);
    margin-left: 15px;
    margin-right: 15px;
  }

  &:last-child {
    @media screen and (max-width: 767px) {
      margin-bottom: 0;
    }
  }
}
.advantages__number {
  margin-bottom: 30px;
  color: var(--text-secondary-color);
  @include font(36px, 41px, 0.04em, 400, var(--font-secondary));
  text-transform: uppercase;
  margin-top: 0;
  @media screen and (min-width: 768px) {
    margin-top: 0;
    @include font(48px, 55px, 0.04em, 400, var(--font-secondary));
    margin-bottom: 13px;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 0;
    @include font(58px, 66px, 0.04em, 400, var(--font-secondary));
    margin-bottom: 15px;
  }

  &::before {
    content: '';
    display: block;
    background-size: contain;
    height: 50px;
    margin-bottom: 16px;
    @media screen and (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media screen and (min-width: 1200px) {
      margin-bottom: 40px;
    }
  }

  &--ice::before {
    background-image: url(../images/advantages/advantages-1-33.png);
    width: 33px;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-1-33@2x.png);
    }
  }

  &--apple::before {
    width: 50px;
    background-image: url(../images/advantages/advantages-2-50.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-2-50@2x.png);
    }
  }
  &--heart::before {
    background-image: url(../images/advantages/advantages-3-44.png);
    width: 44px;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/advantages/advantages-3-44@2x.png);
    }
  }
}

.advantages__text {
  color: var(--text-content-primary-color);
  @include font(12px, 20px, 0.04em, 700, var(--font-primary));
  display: block;
  max-width: 230px;
  margin-top: 0;
  @media screen and (min-width: 768px) {
    @include font(14px, 24px, 0.04em, 700, var(--font-primary));
    max-width: 180px;
  }
  @media screen and (min-width: 1200px) {
    @include font(16px, 28px, 0.02em, 700, var(--font-primary));
    max-width: 330px;
  }
}
